<template>
  <div id="appsad">
    <div class="bumo" @mousemove="resetTimers" @keydown="resetTimers" @click="resetTimers">
      <TopMenu :isAuthenticated="isAuthenticated" :timeLeft="timeLeft" :userInfo="userInfo" @logout="handleLogout" />
      <subMenu :isAuthenticated="isAuthenticated" :timeLeft="timeLeft" />
      <navMenu :isAuthenticated="isAuthenticated" :timeLeft="timeLeft" class="nav-menu" />
      <router-view @user-activity="resetTimers" @login="handleLogin" class="router-view"></router-view>
    </div>
  </div>
</template>

<script>
import TopMenu from './components/TopMenu.vue';
import subMenu from './components/subMenu.vue';
import navMenu from './components/navMenu.vue';
import axios from 'axios';

export default {
  name: 'App',
  components: {
    TopMenu,
    subMenu,
    navMenu
  },
  data() {
    return {
      isAuthenticated: false,
      timeLeft: 7200,
      timer: null,
      logoutTimer: null,
      userInfo: { username: 'Guest' } // 기본값 설정
    };
  },
  methods: {
    async handleLogin() {
      this.isAuthenticated = true;
      await this.fetchUserInfo();
      this.startTimers();
    },
    handleLogout() {
      this.isAuthenticated = false;
      this.userInfo = { username: 'Guest' }; // 로그아웃 시 기본값으로 설정
      localStorage.removeItem('token');
      localStorage.removeItem('userid');
      this.clearTimers();
      this.$router.push({ name: 'mLogin' });
    },
    async fetchUserInfo() {
      try {
        const token = localStorage.getItem('token');
        const uid = localStorage.getItem('userid');
        const response = await axios.get('http://localhost:8001/api/protected/userinfo', {
          headers: {
            Authorization: `Bearer ${token}`,
            userid: uid
          }
        });
        this.userInfo = response.data;
      } catch (error) {
        console.error('Failed to fetch user info:', error);
        this.handleLogout();
      }
    },
    logout() {
      this.handleLogout();
      alert("로그아웃 되었습니다");
    },
    startTimers() {
      this.clearTimers();
      this.startTimer();
      this.startLogoutTimer();
    },
    startTimer() {
      this.timer = setInterval(() => {
        if (this.timeLeft > 0) {
          this.timeLeft--;
        } else {
          clearInterval(this.timer);
        }
      }, 1000);
    },
    startLogoutTimer() {
      this.logoutTimer = setTimeout(() => {
        this.logout();
      }, 7200000); // 2시간 = 7200000ms
    },
    clearTimers() {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      if (this.logoutTimer) {
        clearTimeout(this.logoutTimer);
        this.logoutTimer = null;
      }
    },
    resetTimers() {
      if (this.isAuthenticated) {
        this.timeLeft = 7200;
        this.startTimers();
      }
    }
  },
  mounted() {
    const token = localStorage.getItem('token');
    if (token) {
      this.handleLogin();
    }
  },
  beforeUnmount() {
    this.clearTimers();
  }
}
</script>

<style>
.bumo {
  height: 1000px;
  overflow: auto;
}

.nav-menu {

  position: relative;
  z-index: 99999;
}

.router-view {
  position: absolute;
  top: 222px;
  z-index: 99;
  width: 100%;
  clear: both;

  box-sizing: border-box;


  overflow: auto;
}
</style>
