// CMD> npm install vue-router@next --save
import { createWebHistory, createRouter } from "vue-router";

import subMenu from '@/components/subMenu.vue'
import Menu2 from '@/components/Menu2.vue'
import HelloWorld from '@/components/HelloWorld.vue'
import GoogleMap from '@/components/GoogleMap.vue'
import TopMenu from '@/components/TopMenu.vue'
import mLogin from '@/components/mLogin.vue'
import mJoin from '@/components/mJoin.vue'
import mcheck from '@/components/mcheck.vue'
import AdminPage from '@/components/admin/AdminPage.vue'
import AdminPage2 from '@/components/admin/AdminPage2.vue'
import AdminPage3 from '@/components/admin/AdminPage3.vue'
import MainPage from '@/components/MainPage.vue'
import CarouselPage from '@/components/CarouselPage.vue'
import navMenu from '@/components/navMenu.vue'



const routes = [
    { path: '/subMenu', name: "subMenu", component: subMenu },
    { path: '/Menu2', name: "Menu2", component: Menu2 },
    { path: '/asd', name: "HelloWorld", component: HelloWorld },
    { path: '/GoogleMap', name: "GoogleMap", component: GoogleMap },
    { path: '/TopMenu', name: "TopMenu", component: TopMenu },
    { path: '/mLogin', name: "mLogin", component: mLogin },
    { path: '/mJoin', name: "mJoin", component: mJoin },
    { path: '/mcheck', name: 'mcheck', component: mcheck },
    { path: '/AdminPage', name: 'AdminPage', component: AdminPage },
    { path: '/AdminPage2', name: 'AdminPage2', component: AdminPage2 },
    { path: '/AdminPage3', name: 'AdminPage3', component: AdminPage3 },
    { path: '/MainPage', name: 'MainPage', component: MainPage },
    { path: '/CarouselPage', name: 'CarouselPage', component: CarouselPage },
    { path: '/navMenu', name: 'navMenu', component: navMenu },

];

const router = createRouter({
    history: createWebHistory(),
    routes,
});

export default router;