<template>
    <div>
        <h1>Signup</h1>
        <form @submit.prevent="signup">
            <div>
                <label for="userid">User ID:</label>
                <input type="text" id="userid" v-model="userid" required>
            </div>
            <div>
                <label for="userpw">Password:</label>
                <input type="password" id="userpw" v-model="userpw" required>
            </div>
            <button type="submit">Sign Up</button>
        </form>
        <p v-if="message">{{ message }}</p>
        <p>현재 상태 : {{ receivedMessages }}</p>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'Menu1A1',
    data() {
        return {
            userid: '',
            userpw: '',
            message: '',
            receivedMessages: '',
        };
    },
    mounted() {
        this.ws = new WebSocket('ws://localhost:8001');

        this.ws.onopen = () => {
            console.log('Connected to WebSocket server [menu2]');
        };

        this.ws.onmessage = (event) => {
            console.log('Received from server:', event.data);
            this.receivedMessages = event.data; // 수신된 메시지를 배열에 추가
        };

        this.ws.onclose = () => {
            console.log('Disconnected from WebSocket server');
        };
    },
    methods: {
        signup() {
            const payload = {
                userid: this.userid,
                userpw: this.userpw
            };

            axios.post('http://localhost:8001/db/insert', payload)
                .then(response => {
                    this.message = response.data;
                })
                .catch(error => {
                    console.error('There was an error!', error);
                    this.message = 'Error occurred during signup';
                });
        }
    }
};
</script>

<style scoped></style>
